.main-container h1 {
  width: 7.5rem;
  margin-top: -3rem;
  margin-left: 42rem;
  color: #fff;
  border-bottom: 0.4px solid white;
  padding-bottom: 1rem;
}

.main-container {
  background-color: #171717;
  border-bottom: 0.1rem solid #f7fd04;
}

.cards {
  display: grid;
  grid-auto-flow: row dense;
  grid-auto-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-template-columns: 4fr 4fr;
  grid-template-rows: 4fr 4fr;
  grid-gap: 3rem 2rem;
  gap: 3rem 2rem;
  grid-template-areas:
    ". ."
    ". .";
  justify-items: center;
  align-items: center;
  padding-bottom: 2rem;
}

.cards .card {
  margin-top: 3rem;
  height: 25rem;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  top: 0;
  /* max-width: 32vw; */
  padding: 2rem 2rem;
  margin: 2rem;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
  border: 0.1rem solid #f2f8f9;
  border-radius: 0.2rem;
}

.card:hover {
  transition: all 0.5s ease-in-out;
  box-shadow: 0px 0.3rem 0.5rem rgba(253, 253, 253, 0.781);
  top: -0.4rem;
  border: 0.1rem solid #cccccc;
  background-color: white;
}

.card-image {
  width: 100%;
  height: 200px;
}

img {
  border-radius: 0.1rem;
  max-width: 100%;
}



.wrapper {
  display: inline-flex;
}

.wrapper .icon {
  position: relative;
  background-color: #fff;
  border-radius: 50%;
  border: 0.5px solid rgba(184, 182, 182, 0.233);
  padding: 1rem;
  margin: 1rem;
  width: 2rem;
  height: 2rem;
  font-size: 2.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: 0 2rem 2rem rgba(0, 0, 0, 0.541);
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.icon-inner {
  margin-top: 0.6rem;
}

.wrapper .tooltip {
  position: absolute;
  top: 0;
  font-size: 1.2rem;
  background-color: #ffffff;
  color: #ffffff;
  padding: 0.4rem 0.6rem;
  border-radius: 0.4rem;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .tooltip::before {
  position: absolute;
  content: "";
  height: 1rem;
  width: 0;
  background-color: #ffffff;
  bottom: -0.3rem;
  left: 50%;
  -webkit-transform: translate(-50%) rotate(45deg);
          transform: translate(-50%) rotate(45deg);
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .icon:hover .tooltip {
  top: -3rem;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.wrapper .icon:hover span,
.wrapper .icon:hover .tooltip {
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
}

.wrapper .github:hover,
.wrapper .github:hover .tooltip,
.wrapper .github:hover .tooltip::before {
  background-color: #0b090a;
  color: #ffffff;
}

.wrapper .visit:hover,
.wrapper .visit:hover .tooltip,
.wrapper .visit:hover .tooltip::before {
  background-color: #00ecfd;
  color: black;
}

